.automated_contracts {
  .container-fluid {
    width: 100%;
    padding: 0;
    margin: 0;
    background: #fff;
  }
  .container {
    max-width: 1440px;
    margin: 0 auto;
    padding: 48px 60px 140px 60px;
    @media screen and (max-width: 1100px) {
      max-width: 1100px!important;
      padding: 32px 16px!important;
    }
    @media screen and (max-width: 768px) {
      max-width: 720px!important;
      padding: 32px 16px!important;
    }
    @media screen and (max-width: 480px) {
      max-width: 375px;
      padding: 32px 16px;
    }
  }
  .intro {
    display: grid;
    grid-template-columns: 10fr 17fr;
    place-items: self-start;
    padding: 40px 0 48px;
    @media screen and (max-width: 768px) {
      grid-template-columns: 1fr;
      padding: 0 0 32px;
    }

    .title {
      padding: 0px;
      @media screen and (max-width: 768px) {
       padding: 0;
      }

      h2 {
        margin: 0 0 16px;
        font-weight: 500;
        color: #0a2244;
      }
    }

    .subtitle {
      padding: 10px 0;
      font-family: "General Sans Variable";
      font-style: normal;
      font-weight: 500;
      font-size: 15px;
      line-height: 20px;
      display: flex;
      align-items: flex-end;
      text-transform: uppercase;
      color: #828fa5;
    }
  }
  .boxes {
    display: flex;
    gap: 30px;
    text-align: center;
    @media screen and (max-width: 768px) {
      flex-direction: column;
      gap: 16px;
      place-items: baseline;
    }

    .box {
      border: 1px solid #e1e4ec;
      border-radius: 12px;
      padding: 60px 40px 48px;
      text-align: center;
      max-width: 640px;
      width: 100%;
      @media screen and (max-width: 768px) {
        border: 0;
        border-bottom: 1px solid #e1e4ec;
        border-radius: 0;
        padding: 0 0 32px;
        width: 350px;
      }
      h3{
        margin: 40px 0 16px;
      }
      img{
        max-width: 80px;
      }
    }
  }
}
